<template>
    <div id="detail">
        <el-row :gutter="12">
            <el-col :span="16">
                <el-card shadow="always" class="">
                    <div class="ghxq">
                        <div class="xmxqtit">
                            <i class="iconfont icon-xiangqing"></i>
                            <span class="tit">项目详情</span>
                            <div class="follow">
                                <el-button v-if="card.follow==1" size="small" icon="el-icon-star-on" @click="cancelFollow()">已关注</el-button>
                                <el-button v-else size="small" icon="el-icon-star-off" @click="isFollow()">重点关注</el-button>
                            </div>
                            <el-button class="btn" size="small" @click="projectTree()" plain><i class="iconfont icon-shu"></i>查看项目树</el-button>
                            <el-button class="btn1" size="small" @click="handleEdit()" plain><i class="el-icon-edit-outline"></i>编辑</el-button>
                        </div>
                        <div class="mc project" style="margin:0;"><span class="left">项目名称：</span><p class="right">{{card.target_name}}</p></div>
                        <div class="yxj project"><span class="left">紧急程度：</span><p class="right">{{card.importancedj}}</p></div>
                        <div class="leader project" v-if="card.upManager"><span class="left">上级领导：</span><p class="right">{{card.upManager}}</p></div>
                        <div class="sjmc project" v-if="card.parent_name"><span class="left">上级规划：</span><p class="right">{{card.parent_name}}</p></div>
                        <div class="date project"><span class="left">项目时间：</span><p class="right">{{card.start_time}}~{{card.end_time}}</p></div>
                        <div class="jfys project"><span class="left">项目经费：</span><p class="right">已消耗 {{card.actual_funds}} 万元 / 预算 {{card.funds}} 万元</p></div>
                        <div class="jfys project"><span class="left">诊改指标：</span><p class="right">{{card.target_stand_name}}</p></div>
                        <div class="jfys project"><span class="left">诊改标准：</span><p class="right">{{card.zgbz}}</p></div>
                        <div class="fzr project"><span class="left">负责人：</span><p class="right">{{card.manager_name}}</p></div>
                        <div class="cyr project"><span class="left">参与人：</span><p class="right">{{card.cyr}}</p></div>

                        <!-- 不可以滑动操作 -->
                        <div class="jd project">
                            <span class="left">进度状态：</span>
                            <p class="right" style="position:relative;">
                            <el-slider
                            v-model="card.progress"
                            disabled>
                            </el-slider>
                            <span style="position:absolute;right:5px;top:-7px;font-size:14px;">{{card.progress}}%</span>
                            </p>
                        </div>
                        <div class="xmjj project"><span class="left">项目简介：</span><p class="right">{{card.target_info}}</p></div>
                    </div>
                    <div class="commitbtn" v-if="xmlevel != 1">
                        <!-- 审核状态开始判断 -->
                        <el-tooltip v-if="card.check_status=='-1'" class="item" effect="dark" :content="tipcontent2" placement="top">
                            <el-button class="disabledbtn" size="small">提交完成审核</el-button>
                        </el-tooltip>
                        <el-button v-else-if="card.check_status=='0' && card.manager_id == user_id" @click="shenhebtn()" size="small">提交完成审核</el-button>
                        <el-tooltip v-else-if="card.check_status=='0' && card.manager_id != user_id" class="item" effect="dark" content="您没有权限操作此项目" placement="top">
                            <el-button class="disabledbtn" size="small">提交完成审核</el-button>
                        </el-tooltip>
                        <div class="dsh" v-else-if="card.check_status=='1' && card.creator_id == user_id">
                            <el-button @click="shenhetongguo()" type="success" size="small">审核通过</el-button>
                            <el-button @click="shenheweitongguo()" type="danger" size="small">审核未通过</el-button>
                        </div>
                        <!-- 负责人与上级领导或自己查看已提交审核的两种状态 -->
                        <el-button v-else-if="card.check_status=='1' && card.creator_id != user_id" type="danger" size="small" disabled>待审核</el-button>
                        <el-button v-else-if="card.check_status=='2'" type="success" @click="shenheFinish" size="small">审核通过</el-button>
                        <el-button v-if="card.check_status=='-2' && card.manager_id != user_id" type="danger" size="small" disabled>审核未通过</el-button>
                        <el-button v-if="card.check_status=='-2' && card.manager_id == user_id && xmlevel != 4" @click="revisejd()" type="danger" size="small">审核未通过</el-button>
                        <el-tooltip v-if="card.check_status=='-2' && card.manager_id == user_id && xmlevel == 4" class="item" effect="dark" content="审核未通过，请完成后重新提交审核" placement="top">
                            <el-button class="disabledbtn" size="small">提交完成审核</el-button>
                        </el-tooltip>
                    </div>
                    <div class="projectNote" style="margin-top:20px;" v-if="xmlevel=='4'">
                        <div class="xmxqtit">
                            <i class="iconfont icon-lishijilu"></i>
                            <span class="tit" v-if="jindujilu">进度记录</span>
                            <span class="tit" v-else>进度记录(暂无)</span>
                        </div>
                        <div v-if="note">

                            <div class="history" v-for="(item,i) in note" :key="i">
                                <div class="history_date">{{item.report_time}}</div>
                                <div class="projectName note"><span class="left">项目名字：</span><p class="right">{{card.target_name}}</p></div>
                                <div class="zhongyaochengdu note"><span class="left">紧急程度：</span><p class="right">{{card.importancedj}}</p></div>
                                <div class="history_jd note"><span class="left">当前进度：</span><p class="right">项目完成{{item.progress}}%</p></div>
                                <div class="history_xh note"><span class="left">本次经费消耗：</span><p class="right">{{item.actual_funds}}万元</p></div>
                                <div class="history_content note"><span class="left">汇报内容：</span><p class="right">{{item.report_desc}}</p></div>
                                <div class="history_file note">
                                    <span class="left">附件：</span>
                                    <div class="right" v-if="item.attachment && item.attachment.length>=1">
                                        <span v-for="(items,i) in item.attachment" :key="i" class="document"><i class="iconfont icon-fujian"></i>{{items.name}}</span>
                                    </div>
                                    <div class="right" v-else>
                                        <span>无附件</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="resolvetask" v-if="xmlevel != '4'">
                        <div class="resolvetit">
                            <i class="iconfont icon-fenjie"></i>
                            <span class="tit" v-if="resolve">本项目的分解任务</span>
                            <span class="tit" v-if="!resolve">本项目的分解任务（暂无）</span>
                        </div>
                        <!-- 分解的任务 -->
                        <div v-if="resolve">
                            <div class="resolve" v-for="(item,i) in resolvetask" :key="i" @click="checkDetail(item.id)">
                                <!-- <div class="taskbtn task">
                                    <el-button @click="checkDetail(item.id,item.level)" type="primary" size="mini">查看详情</el-button>
                                    <el-button v-if="item.check.code==1" @click="daishenhebtn(item.id,item.level)" type="danger" size="mini">待审核</el-button>
                                    <el-button v-if="item.check.code==2" @click="shenheFinish()" type="success" size="mini">审核通过</el-button>
                                </div> -->
                                <div class="taskname task"><span class="left">任务名称：</span><p class="right">{{item.target_name}}</p></div>
                                <div class="taskfzr task"><span class="left">负责人：</span><p class="right">{{item.manager_name}}</p></div>
                                <div class="taskimportant task"><span class="left">经费消耗：</span><p class="right">{{item.actual_funds}}万元/{{item.funds}}万元</p></div>
                                <div class="taskbtn task" v-if="item.check_status!=-2 && item.check_status!=-1 && item.check_status!=0">
                                    <span class="left">审核状态：</span>
                                    <span class="right">
                                        <span class="dsh sh" v-if="item.check_status==1">待审核</span>
                                        <span class="tgsh sh" v-if="item.check_status==2">审核通过</span>
                                    </span>
                                </div>
                                <div class="taskjd task" v-else-if="item.check_status=='-2' || item.check_status=='-1' || item.check_status=='0'">
                                    <span class="left">进度状态：</span>
                                    <p class="right" style="position:relative;">
                                        <el-slider v-model="item.progress" disabled>
                                        </el-slider>
                                        <span style="position:absolute;right:5px;top:-7px;font-size:14px;">{{item.progress}}%</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="shenbaojilu" v-if="xmlevel!=1">
                        <div class="shenbaotit">
                            <i class="iconfont icon-huibaojilu"></i>
                            <span class="tit" v-if="shenbaojilu">申报记录</span>
                            <span class="tit" v-else>申报记录（暂无）</span>
                        </div>
                        <div class="shenbao_history" v-for="(item,k) in shenbao" :key="k">
                            <!-- <div class="xjsh" v-if="item.shenhe">{{item.shenhe}}</div>
                            <div class="sjsb" v-if="item.shenbao">{{item.shenbao}}</div> -->
                            <div class="shenbao_header">
                                <div class="shenbao_time">{{item.declare_time}}</div>                              
                            </div>
                            <div class="shenbao_xm shenbao"><span class="left">项目名称：</span><p class="right">{{card.target_name}}</p></div>
                            <div class="shenbao_result shenbao">
                                <span class="left">申报结果：</span>
                                <p class="right">
                                    <span class="result" v-if="item.result=='-1'" style="background:#CAE3FA;color:#358FE2">待审核</span>
                                    <span class="result" v-if="item.result=='1'" style="background:#E4FCD0;color:#7CB349;">成功</span>
                                    <span class="result" v-if="item.result=='0'" style="background:#FDD6D9;color:#E42739;">失败</span>
                                </p></div>
                            <div class="shenbao_huifu shenbao"><span class="left">申报评语：</span><p class="right">{{item.comment}}</p></div>
                            <div class="shenbao_fzr shenbao"><span class="left">负责人：</span><p class="right">{{item.manager_name}}</p></div>
                            <div class="shenbao_jf shenbao"><span class="left">经费消耗：</span><p class="right">{{item.actual_funds}}万元/{{item.funds}}万元</p></div>
                            <div class="shenbao_content shenbao"><span class="left">汇报内容：</span><p class="right">{{item.report_desc}}</p></div>
                            <div class="shenbao_file shenbao">
                                <span class="left">附件：</span>
                                <div class="right" v-if="item.files && item.files.length>=1">
                                    <span v-for="(items,i) in item.files" :key="i" class="document"><i class="iconfont icon-fujian"></i>{{items.name}}</span>
                                </div>
                                <div class="right" v-else>
                                    <span>无附件</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- 判断user_id是否与managerid匹配并且resmess为0 -->
                    <!-- <div class="jdReport" v-if="xmlevel=='4'"> -->
                    <div class="jdReport" v-if="localId == card.manager_id && resmess == '0'">
                        <div class="xmxqtit">
                            <i class="iconfont icon-huibaojilu"></i>
                            <span class="tit">进度汇报</span>
                        </div>
                        <div class="report_content report">
                            <span class="left">汇报内容：</span>
                            <div class="right">
                                <el-input
                                type="textarea"
                                :autosize="{ minRows: 2}"
                                placeholder="建议不超过200字"
                                v-model="report.content">
                                </el-input>
                            </div>
                        </div>
                        <div class="report_jd report">
                            <span class="left">进度变化量：</span>
                            <div class="right">
                                <el-input-number v-model="report.jd" @change="numChange" size="small" :min="minNum" :max="maxNum" label=""></el-input-number>
                                <div style="font-size:12px;display:inline-block;margin-left:10px;">（可输入范围：{{minNum}}~{{maxNum}}）</div>
                            </div>
                        </div>
                        <div class="report_xh report">
                            <span class="left">本次经费消耗：</span>
                            <div class="right" style="width:200px;">
                                <el-input type="number" style="width:130px;" v-model="report.xh" size="small" :min="0" label=""></el-input>
                                　万元
                            </div>
                        </div>
                        <div class="report_files report">
                            <span class="left">汇报附件：</span>
                            <el-upload
                                class="upload-demo right"
                                ref="upload"
                                drag
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :on-preview="handlePreview"
                                :on-remove="handleRemove"
                                :on-success="handleSuccess"
                                :on-error="handleError"
                                :on-change="handleChange"
                                :before-upload="handleUpload"
                                :auto-upload="false"
                                multiple>
                                <i class="el-icon-upload"></i>
                                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            </el-upload>
                        </div>
                        <el-button class="submitbtn" size="small" type="success" @click="submitUpload">提交</el-button>
                        <el-button class="backbtn" size="small" @click="back()" plain>返回</el-button>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8" class="right_card">
                <el-card>
                    <div class="tipstit">诊改标准库速查</div>
                    <div class="search">
                        <el-input
                            placeholder="请输入搜索内容"
                            prefix-icon="el-icon-search"
                            v-model="searchContent"
                            style="margin-right: 5px;"
                            @keyup.enter.native="submit"
                            clearable>
                        </el-input>
                        <el-button plain @click="quickQuery()" style="background:#fdfdfd;">搜索</el-button>
                    </div>
                    <div class="tree" v-if="tree">
                        <el-tree
                        :data="navdata"
                        :props="defaultProps"
                        @node-click="handleNodeClick">
                        </el-tree>
                    </div>
                    <div class="tree" v-else>
                        <el-tree
                        :data="navdata2"
                        :props="defaultProps2"
                        @node-click="handleNodeClick2">
                        </el-tree>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-dialog
        title="提交完成审核"
        :visible.sync="dialogShenhe"
        width="60%">
            <div class="shenhe_content shenhe">
                <span class="left">汇报内容：</span>
                <div class="right">
                    <el-input
                    type="textarea"
                    :autosize="{ minRows: 2}"
                    placeholder="建议不超过200字"
                    v-model="shenhe.content">
                    </el-input>
                </div>
            </div>
            <div class="shenhe_files shenhe">
                <span class="left">汇报附件：</span>
                <el-upload
                    class="upload-demo right"
                    ref="uploadshenhe"
                    drag
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :on-success="handleSuccess"
                    :on-error="handleError"
                    :on-change="handleChange"
                    :before-upload="handleUpload"
                    :auto-upload="false"
                    multiple>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                </el-upload>
            </div>
            <el-dialog
            title="操作提示"
            :visible.sync="innerdialogshenhe"
            width="50%"
            append-to-body>
                <span>您只有一次提交审核的机会，确定工作已经完成了吗？</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="innerdialogshenhe = false">取 消</el-button>
                    <el-button type="primary" @click="shenheSure()">确 定</el-button>
                </span>
            </el-dialog>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogShenhe = false">取 消</el-button>
                <el-button type="primary" @click="innerShenhe()">确 定</el-button>
            </span>
        </el-dialog>
        <!-- <el-dialog
        title="提交完成审核"
        :visible.sync="dialogTaskshenhe"
        width="60%">
            <span>您确定工作已经全部完成了吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogTaskshenhe = false">取 消</el-button>
                <el-button type="primary" @click="taskShenhe()">确 定</el-button>
            </span>
        </el-dialog> -->
        <el-dialog title="审核通过" :visible.sync="dialogshtg" width="60%">
            <div class="shpy shtg">
                <span class="left">审核通过评语：</span>
                <span class="right">
                    <el-input
                    type="textarea"
                    :autosize="{ minRows: 4}"
                    placeholder="建议不超过200字"
                    v-model="finish.passcontent">
                    </el-input>
                </span>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogshtg = false">取 消</el-button>
                <el-button type="primary" @click="passSure()">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog title="审核未通过" :visible.sync="dialogshwtg" width="60%">
            <div class="shwtgpy shwtg">
                <span class="left">审核未通过评语：</span>
                <span class="right">
                    <el-input
                    type="textarea"
                    :autosize="{ minRows: 4}"
                    placeholder="建议不超过200字"
                    v-model="finish.noPasscontent">
                    </el-input>
                </span>
            </div>
            <div class="htjd shwtg" style="margin-top:20px;">
                <span class="left" style="line-height:40px;">建议进度：</span>
                <span class="right" style="position:relative;">
                    <el-slider v-model="finish.htjd">
                    </el-slider>
                    <span style="position:absolute;right:5px;top:-7px;font-size:14px;">{{finish.htjd}}%</span>
                </span>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogshwtg = false">取 消</el-button>
                <el-button type="primary" @click="nopassSure()">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog class="dialoghtxmjd" title="请选择回退项目进度" :visible.sync="dialoghtxmjd" width="60%">
            <div class="htjd">
                <div class="fktit tit">审核评语：</div>
                <div class="shenbaopy ht">
                    <p class="pingyu right">{{noPass.shpy}}</p>
                </div>
            </div>
            <div class="htjd">
                <div class="zxmtit tit">本项目的分解任务</div>
                <ul class="tasklist">
                    <li class="list" v-for="(item,i) in noPass.resolvetask1" :key="i">
                        <div class="proname ht" @click="openhidden(i)"><span class="left"><i class="icon el-icon-caret-right"></i>项目名称：</span><span class="right">{{item.target_name}}</span></div>
                        <div class="hidden">
                            <div class="shenbao_htjd ht">
                                <span class="left">建议进度：</span>
                                <span class="right">
                                    <el-slider v-model="item.progress"></el-slider>
                                </span>
                            </div>
                            <div class="jd ht">
                                <span class="left">回退评语：</span>
                                <span class="right">
                                    <el-input type="textarea" :autosize="{ minRows: 2}" placeholder="建议不超过200字" v-model="item.msg">
                                    </el-input>
                                </span>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
            <el-dialog title="提示" :visible.sync="innerdialoghtxmjd" width="50%" append-to-body>
            <span>未通过审核的项目只有一次回退本项目分解的任务机会，请确认您是否将应回退的任务都选择完了？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="htxmjd()">是</el-button>
                <el-button type="primary" @click="innerdialoghtxmjd = false">否</el-button>
            </span>
            </el-dialog>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialoghtxmjd = false">取 消</el-button>
                <el-button type="primary" @click="htjdSure()">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name:'detail',
    inject:["reload"],
    data(){
        return{
            localId: null,
            resmess: '',
            tree:true,
            dialogShenhe: false,
            innerdialogshenhe: false,
            // dialogTaskshenhe: false,
            dialogshtg: false,      //审核通过
            dialogshwtg: false,     //审核未通过
            dialoghtxmjd: false,     //审核未通过分解任务进度
            innerdialoghtxmjd: false,
            fileUpload: false,
            xmlevel: null,
            route: null,
            xmjd: true,
            id: null,   //项目id
            user_id: null,   //登录用户id
            resolve: false,         //分解任务
            shenbaojilu: false,     //申报记录
            jindujilu: false,       //进度记录
            tipcontent1: '审核未通过，请仔细审阅分解任务进行编辑',     //审核未通过，请仔细审阅子规划 项目 任务进行编辑
            tipcontent2: '分解任务进度全达到100%才可提交审核',     //分解出来的规划 项目 任务均完成才可提交审核
            card:{
                // level: '2',
                // id: '1',
                // mc:'项目名称项目名称',
                // important: '重要',
                // follow: 1,
                // leader: '李老师',
                // sj: {gh: '上级规划',mc: '上级规划名称上级规划名称'},
                // start: '2018.07.07',
                // end: '2018.10.11',
                // jfxh: 1.10,
                // jfys: 2.20,
                // fzr: '张老师',
                // cyr: '王老师、周老师、赵老师',
                // zgzb: '在校学生数',
                // zgbz: '≥3000',
                // jd: 30,
