        submitUpload(){
            if(this.report.content == ''){
                this.$message.warning('汇报内容不能为空！');
            }else if(this.report.xh ==''){
                this.$message.warning('经费消耗不能为空！');
            }else{
                var _this = this
                if(_this.card.importancedj == "重要"){
                    var importancedj = 1
                }else if(_this.card.importancedj == "普通"){
                    var importancedj = 2
                }else if(_this.card.importancedj == "暂缓"){
                    var importancedj = 3
                }
                console.log(this.report)    //将进度和内容发送给后台，完成后提交附件
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/mb/report.action",
                    data: {
                        'report_name':_this.card.target_name,
                        'importancedj':importancedj,
                        'actual_funds':_this.report.xh,
                        'report_desc':_this.report.content,
                        'target_id':_this.card.id,
                        'manager_id':_this.card.manager_id,
                        'progress':_this.report.jd
                    },
                    dataType: "json",
                    success: function (res) {
                        console.log(res)
                        if(res.mess == true){
                            if(_this.report.files.length >= 1){
                                _this.$refs.upload.submit();
                            }else{
                               _this.$notify({
                                    title: '提交成功',
                                    type: 'success',
                                });
                                console.log(_this.report)
                                //提交成功后重新获取一次数据赋值给页面
                                _this.$router.go(0)
                            }
                        }
                    }
                });
            }
        },
