        handleRemove(file, fileList) {
            console.log(file, fileList);
            if(file.size / 1024 / 1024 > 5){
                this.$message.warning(file.name+'超过5MB已强制移除!');
            }else{
                this.$message.success(file.name+'已移除');
            }
            
        },
        handlePreview(file) {      //点击文件列表中已上传的文件时的钩子
            console.log(file);
        },
        handleChange(file, fileList){      //文件列表数据改变后的回调
            console.log(file, fileList);
            for(var i = 0;i < fileList.length;i++){
                var size = file.size / 1024 / 1024 > 5
                if(size){
                    this.$message.warning(file.name+'文件大小不能超过5MB!');
                }
            }
            if(!this.dialogShenhe){
                this.report.files.push(file.name)
            }else{
                this.shenhe.files.push(file.name)
            }
        },
        handleUpload(file){
            console.log(file)
            var size = file.size / 1024 / 1024 <= 5     //文件大小不能超过5M
            if(!size){
                this.$message.error(file.name+'超过5MB已强制移除!');
            }
            return size;
