        revisejd(){         //打开回退下级项目对话框
            var _this = this
            _this.noPass.shpy = ''
            _this.noPass.resolvetask1 = []
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/mb/goRegression.action",
                data: {"targetid":_this.card.id},
                dataType: "json",
                success: function (res) {       //获取回退的数据
                    console.log(res)
                    if(res.comment && res.targetlist){
                        _this.noPass.shpy = res.comment
                        for(var i = 0;i < res.targetlist.length;i++){
                            res.targetlist[i].msg = ''
                            _this.noPass.resolvetask1.push(res.targetlist[i])
                        }
