            if(_this.noPass.shpy){
                var data = []
                for(var i = 0;i < _this.noPass.resolvetask1.length;i++){
                    data.push({'comment':_this.noPass.resolvetask1[i].msg,'name':_this.noPass.resolvetask1[i].target_name,'manager_id':_this.noPass.resolvetask1[i].manager_id,'target_id':_this.noPass.resolvetask1[i].id,'progress':_this.noPass.resolvetask1[i].progress})
                }
            }
            console.log(JSON.stringify(data))
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/mb/checkRegression.action",
                data: JSON.stringify(data),
                contentType: 'application/json;charset=UTF-8',
                dataType: "json",
                success: function (res) {       //回退子项目的数据
                    console.log(res)
                    if(res.mess == true){
                        _this.$router.go(0)
                    }else{
                        _this.$message.error('回退失败请重试！')
                    }
                }
            });
            //回退任务进度向后台发起请求

        },
        passSure(){
            //向后台发送数据通过审核
            if(this.finish.passcontent == ''){
                this.$message.warning('审核通过评语不能为空！')
            }else{
                var _this = this
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/mb/checktarget.action",
                    data: {"comment":_this.finish.passcontent,"result":1,"target_id":_this.card.id,"propress":'',},
                    dataType: "json",
                    success: function (res) {
                        console.log(res)
                        if(res.mess == true){
                            // _this.$message.success('工作审核通过设置成功！')
                            // _this.card.check_status = 2
                            _this.$router.go(0)
                        }else{
                            _this.$message.success('工作审核通过设置失败请重试！')
                        }
                    }
                    
                });
            }
