                this.$message.warning('请给出建议进度！')
            }else{
                var _this = this
                console.log(_this.finish.htjd)
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/mb/checktarget.action",
                    data: {"comment":_this.finish.noPasscontent,"result":0,"target_id":_this.card.id,"progress":_this.finish.htjd,},
                    dataType: "json",
                    success: function (res) {
                        console.log(res)
                        if(res.mess == true){
                            _this.$router.go(0)
                        }else{
