                        }
                    }
                    
                });
                this.dialogshwtg = false
                this.card.shenhe = -2
                this.card.jd = this.finish.htjd
                if(this.xmlevel == 4){
                    this.tipcontent1 = '任务完成才可提交审核'
                }
                this.finish.noPasscontent == ''
            }
        },
        openhidden(i){       //展开分解的项目
            var block = $('.hidden').eq(i).css('display')
            // console.log(i,block)
            if(block == 'block'){
                $('.hidden').eq(i).css('display','none')
                $('.icon').eq(i).removeClass('el-icon-caret-bottom').addClass('el-icon-caret-right')
            }else if(block == 'none'){
                $('.hidden').eq(i).css('display','block')
                $('.icon').eq(i).removeClass('el-icon-caret-right').addClass('el-icon-caret-bottom')
            }
        },
        init(){
            console.log(this.$route.query)      //用传过来的值向后台发起请求将获取到的数据赋值给页面
            localStorage.setItem("idTree",this.$route.query)
            this.id = this.$route.query.id
            this.user_id = window.sessionStorage.getItem('user_id')
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/mb/targetDetails.action",
                data: {'targetid':_this.id,'user_id':_this.user_id},
                dataType: "json",
                success: function (res) {
                    console.log(res, 'resres')
                    sessionStorage.setItem('shifoufenjie', res.targetDetails.progress);
                    _this.xmlevel = res.targetDetails.level
                    if(res.targetDetails.importancedj == 1){
                        res.targetDetails.importancedj= '重要'
                    }else if(res.targetDetails.importancedj == 2){
                        res.targetDetails.importancedj= '普通'
                    }else if(res.targetDetails.importancedj == 3){
                        res.targetDetails.importancedj= '暂缓'
                    }
                    
                    _this.card = res.targetDetails;
                    // if (res.mess == '此目标还未分解子目标！') {
                    //     _this.resmess = 0
                    // }else{
                    //     _this.resmess = 1
                    // }
                    // console.log(_this.resmess, 'resmess')
                    _this.resmess = res.mess;
                    if(res.targetDetails.parent_id==0){
                        _this.card.upManager='无'
                    }
                    if(res.targetDetails.level == 1){
                        _this.card.sj = ''
                    }else if(res.targetDetails.level == 2){
                        _this.card.sj = '上级规划'
                    }else if(res.targetDetails.level == 3){
                        _this.card.sj = '上级工作'
                    }else if(res.targetDetails.level == 4){
                        _this.card.sj = '上级项目'
                    }
                    if(res.targetDetails.target_stand_contion == 1){
                        _this.card.zgbz = '＞'+ res.targetDetails.target_stand_info
                    }else if(res.targetDetails.target_stand_contion == 2){
                        _this.card.zgbz = '≥'+ res.targetDetails.target_stand_info
                    }else if(res.targetDetails.target_stand_contion == 3){
                        _this.card.zgbz = '＜'+ res.targetDetails.target_stand_info
                    }else if(res.targetDetails.target_stand_contion == 4){
                        _this.card.zgbz = '≤'+ res.targetDetails.target_stand_info
                    }else if(res.targetDetails.target_stand_contion == 5){
                        _this.card.zgbz = '＝'+ res.targetDetails.target_stand_info
                    }else if(res.targetDetails.target_stand_contion == 6){
                        _this.card.zgbz = '≠'+ res.targetDetails.target_stand_info
                    }else{
                        _this.card.zgbz = res.targetDetails.target_stand_info
                    }
                    if(res.targetDetails.userEntity){
                        // console.log(res.targetDetails.userEntity)
                        for(var i = 0;i < res.targetDetails.userEntity.length;i++){
                            if (i == 0){
                                _this.card.cyr = res.targetDetails.userEntity[i].name + '、'
                            } else if ( i == res.targetDetails.userEntity.length-1 ){
                                _this.card.cyr = _this.card.cyr + res.targetDetails.userEntity[i].name
                            } else {
                                _this.card.cyr = _this.card.cyr + (res.targetDetails.userEntity[i].name + '、')
                            }
                        }
                    }

                    if(res.subprojects){      //本任务的分解任务
                        if(res.subprojects.length!=0){
                            _this.resolve = true
                            _this.resolvetask = res.subprojects
                        }else{
                            _this.resolve = false
                        }
                    }

                    if(res.declareList){          //本任务的申报记录
                        if(res.declareList.length!=0){
                            _this.shenbaojilu = true
                            if(res.declareList.importancedj == 1){
                                res.declareList.importancedj = '重要'
                            }else if(res.declareList.importancedj == 2){
                                res.declareList.importancedj = '普通'
                            }else if(res.declareList.importancedj == 3){
                                res.declareList.importancedj = '暂缓'
                            }
                            _this.shenbao = res.declareList
                        }else{
                            _this.shenbaojilu = false
                        }
                    }

                    if(res.reportlist){
                        if(res.reportlist.length!=0){
                            _this.jindujilu = true
                            _this.note = res.reportlist
                        }else{
                            _this.jindujilu = false
                        }
                    }

                    if(_this.xmlevel == 2){
                        _this.tipcontent2 = '分解出来的项目均完成才可提交审核'
                    }else if(_this.xmlevel == 3){
                        _this.tipcontent2 = '分解出来的任务均完成才可提交审核'
                    }else if(_this.xmlevel == 4){
                        _this.tipcontent2 = '任务完成才可提交审核'
                        _this.tipcontent1 = '任务完成才可提交审核'
                    }
                    _this.minNum = 0 - _this.card.progress  //进度变化量最小值
                    _this.maxNum = 100 - _this.card.progress    //进度变化量最大值
                }

            });
        },
        
    },
    mounted() {
        this.localId = localStorage.getItem('user_id');
        console.log(this.localId, 'localId')
        // var zxc = sessionStorage.getItem('user_id');
        // console.log(asd, 'asd');
        // console.log(zxc, 'zxc');
        var bjid = localStorage.getItem("creid");
        var usdid = window.sessionStorage.getItem('user_id')
        var btn1 = document.getElementsByClassName("btn1");
        if(bjid == usdid){
            btn1[0].style.backgroundColor = "#85CE61";
        }else{
            btn1[0].style.backgroundColor = "#cccccc";
        }
        this.init()
        var that = this;
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/standard.action",
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.navdata = res.standardlist;
            }
        })
    },
    watch: {
        '$route':{
            handler(route){
                var _this = this
                // console.log(route,_this)
                if(route.name == 'detail'){
                    _this.init()
                }
            },
            deep: true
        }
    },
}
</script>

